@font-face {
  font-family: 'TTLakesRegular';
  src: url(./fonts/TTLakes-Medium.ttf);
  font-weight: normal; }

@font-face {
  font-family: 'TTLakesLight';
  src: url(./fonts/TTLakes-Light.ttf);
  font-weight: 300; }

@font-face {
  font-family: 'TTLakesBold';
  src: url(./fonts/TTLakes-Bold.ttf);
  font-weight: 600; }

@font-face {
  font-family: 'TTLakesRegular';
  src: url(./fonts/TTLakes-Medium.ttf);
  font-weight: normal; }

@font-face {
  font-family: 'TTLakesLight';
  src: url(./fonts/TTLakes-Light.ttf);
  font-weight: 300; }

@font-face {
  font-family: 'TTLakesBold';
  src: url(./fonts/TTLakes-Bold.ttf);
  font-weight: 600; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

button {
  border: none;
  cursor: pointer; }

a {
  cursor: pointer; }

input,
textarea {
  font-family: 'TTLakesRegular';
  padding-left: 8px;
  padding-right: 10px;
  resize: none; }

::-webkit-input-placeholder {
  padding-left: 8px; }

:-moz-placeholder {
  padding-left: 8px; }

::-moz-placeholder {
  padding-left: 8px; }

:-ms-input-placeholder {
  padding-left: 8px; }

body::-webkit-scrollbar {
  display: none; }

main.active,
header.active {
  filter: grayscale(87%) blur(6px) opacity(80%);
  -webkit-filter: grayscale(87%) blur(6px) opacity(80%);
  -moz-filter: grayscale(87%) blur(6px) opacity(80%);
  transition: 0.3s ease-in-out; }

header.active {
  z-index: 0; }

main,
header {
  transition: 0.3s ease-in-out; }

body {
  background: #F8F8F8;
  display: block; }
  body.modal-open {
    overflow-y: hidden; }

a {
  text-decoration: none; }

ul {
  text-decoration: none;
  list-style: none; }

li {
  line-height: 2.7; }

input {
  outline: none; }

input[type="radio"]:checked,
input[type="radio"]:not(:checked) {
  opacity: 0; }

input[type="radio"]:checked + label {
  color: #41F6D7; }

.brands-section {
  position: relative;
  width: 100%;
  height: auto;
  padding-top: 50px; }
  .brands-section.brands-section__title {
    position: relative;
    height: auto;
    background: linear-gradient(10deg, #F8F8F8 0%, #FFFFFF 98.61%);
    padding: 17px;
    margin-left: -17px;
    width: 100vw;
    font-family: 'TTLakesLight';
    color: #7E7E82;
    font-size: 16px; }

.swiper {
  width: 100%;
  min-height: 110px;
  z-index: 0;
  position: relative; }
  .swiper.swiper-showed.repear {
    height: 200px; }
    .swiper.swiper-showed.repear.names {
      height: auto;
      min-height: 250px; }

.swiper-slide {
  background-color: #fff;
  height: 72px;
  display: flex;
  width: 240px;
  align-items: center;
  min-width: 240px !important; }
  .swiper-slide.brands-cards {
    border-radius: 6px;
    border: 1px solid #EAEAEA; }
    .swiper-slide.brands-cards.variety-repear {
      height: 160px; }
      .swiper-slide.brands-cards.variety-repear.services {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        width: 260px;
        height: 210px;
        padding: 16px;
        border-radius: 8px; }

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  text-align: center;
  line-height: 20px;
  font-size: 12px;
  opacity: 1;
  background-color: #DDDDDD; }

.swiper-pagination-bullet-active {
  background: #B5B6BC; }

.around-btn-slide {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px; }

.brands-logo {
  height: 40px; }
  .brands-logo.apple-logo {
    height: 44px; }
  .brands-logo.bosch-logo {
    height: 32px; }
  .brands-logo.hp-logo {
    height: 54px; }
  .brands-logo.acer-logo {
    height: 22px; }
  .brands-logo.viewsonic-logo {
    height: 18px; }
  .brands-logo.sony-logo {
    height: 20px; }

.dropdown-pink {
  width: 8px; }

.slider__btn {
  width: 30%;
  justify-content: center;
  display: flex;
  height: 100%;
  align-items: center; }
  .slider__btn.variety-repear__btn-repear {
    align-items: flex-end;
    padding-bottom: 16px; }

.slider__logo {
  display: flex;
  width: 70%;
  padding-left: 16px;
  align-items: center;
  height: 100%; }
  .slider__logo.smallest-logo {
    justify-content: flex-start;
    padding-left: 16px; }
  .slider__logo.variety-repear__name-repear {
    align-items: flex-start;
    padding-top: 16px;
    font-family: 'TTLakesRegular';
    font-size: 16px; }

.computer-view.tablet-view {
  display: none; }

@media screen and (min-width: 700px) {
  .computer-view.tablet-view {
    display: inline-flex; } }

.ellipse-btn-repear {
  color: #fff;
  background-color: #FF3E79;
  padding: 8px 15px 8px 15px;
  font-size: 12px;
  font-family: 'TTLakesRegular';
  border-radius: 16px;
  transition: transform 0.2s ease-in-out; }
  .ellipse-btn-repear:hover {
    transform: scale(1.1);
    -webkit-filter: drop-shadow(5px 5px 5px rgba(221, 12, 82, 0.6));
            filter: drop-shadow(5px 5px 5px rgba(221, 12, 82, 0.6)); }

.title-repear {
  display: block;
  font-family: 'TTLakesLight';
  font-size: 14px; }
  .title-repear.terms {
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    width: calc(100% - 5px); }

.name-repear {
  color: #7E7E82;
  font-family: 'TTLakesRegular'; }

.info-repear {
  color: #000;
  font-family: 'TTLakesLight'; }

.title-repear__services {
  display: none; }

.post-title {
  width: 100%;
  margin-top: 24px;
  font-family: 'TTLakesLight';
  padding-bottom: 16px; }
  .post-title.commercial {
    font-family: 'TTLakesRegular';
    height: 50px;
    max-width: 320px;
    display: flex;
    align-items: center;
    justify-content: space-between; }

.footer {
  width: 100%;
  height: auto;
  margin-top: 50px;
  padding-top: 40px;
  display: inline-flex;
  padding-left: 16px;
  padding-bottom: 24px;
  display: flex;
  justify-content: space-between; }

.footer-copyright-info {
  font-family: 'TTLakesLight';
  color: #7E7E82;
  font-size: 14px;
  width: 100%; }

#show-all-brands-section-icon.rotate {
  transform: rotate(180deg); }

#swiper-brand-section.showed {
  height: auto; }

.tablet-view {
  display: none; }

@media screen and (min-width: 700px) {
  .tablet-view {
    display: inline-flex; } }

#show-all-brands-section-icon-variety.rotate {
  transform: rotate(180deg); }

#swiper-variety.showed {
  height: auto; }

.variety-repear__name-repear {
  color: #000; }
